<template>
	<div>
		 <van-circle v-model="currentRate"  :rate="rt" :speed="100" :text="text" stroke-width="100" layer-color="#f5f5f5" :color="gradientColor"/>
	</div>
</template>

<script>
	export default{
		props:['rt'],
		data(){
			return{
				 currentRate: 0,
				 gradientColor: {
				         '0%': '#3fecff',
				         '100%': '#40ED94',
				 },
			}
		},
		computed: {	  
		    text() {
		      return this.currentRate.toFixed(0) + '%';
		    },
		  },
	}
</script>

