<template>
	<div class="newsContainer">
	  <!---->
	  <van-nav-bar title="" left-text="" left-arrow fixed @click-left="$router.back()"/>
	  <div class="topContainer">
	    <div class="leftPic"><img src="../assets/imgs/alertPic.png"></img></div>
	    <div class="alertTitleContainer">
	    <div class="alertName">{{title}}</div>
	    <div class="alertInfo">
	      <div class="alertInfo_school"><van-icon class="newsIcon" name="wap-home-o" color="#ffffff" custom-style="font-weight:bold"/>{{author}}</div>
	      <div class="alertInfo_time"><van-icon class="newsIcon" name="clock-o" color="#ffffff" custom-style="font-weight:bold"/>{{time}}</div>
	    </div>
	  </div>
	  </div>
	  <div class="alertDetailContainer">
	    {{title}}
	  </div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default{
		props:['art_id'],
		data(){
			return{
				title:'',
				author:'',
				time:'',
				content:''
			}
		},
		methods:{
			//
			async getNewsDetail(){
				let article_id = this.art_id
				const res = await axios.get('http://toutiao.itheima.net/v1_0/articles/'+article_id)
				if(res.status == 200){
					this.title = res.data.data.title
					this.author = res.data.data.aut_name
					this.time = res.data.data.pubdate
					this.content =  res.data.data.content
				}
			}
		},
		created(){
			this.getNewsDetail()
		}
	}
</script>

