import Vue from 'vue'
import VueRouter from 'vue-router'	
import Home from '../views/Home.vue'
import NewsAll from '../views/NewsAll.vue'
import NewsDetail from '../views/NewsDetail.vue'
import Ksyy from '../views/Ksyy.vue'
import Crjl from '../views/Crjl.vue'
import Wdyy from '../views/Wdyy.vue'
import Bxcg from '../views/Bxcg.vue'
import CgPage from '../views/CgPage.vue'
import MyCenter from '../views/MyCenter.vue'
import MyCenterRank from '../views/MyCenterRank.vue'
import JcRecord from '../views/JcRecord.vue'
import JcRecordDetail from '../views/JcRecordDetail.vue'
import FaceImport from '../views/FaceImport.vue'
import FaceImportH5 from '../views/FaceImportH5.vue'
import Xflimit from '../views/Xflimit.vue'
import DkInfo from '../views/DkInfo.vue'
import DkInfoH5 from '../views/DkInfoH5.vue'

Vue.use(VueRouter)

const routes = [
  {path: '/',name: 'Home',component: Home},
  {path: '/newsall/',name: 'NewsAll',component: NewsAll},
  {path: '/newsdetail/:art_id',name: 'NewsDetail',component: NewsDetail,props:true},
  {path: '/ksyy/',name: 'Ksyy',component: Ksyy},
  {path: '/crjl/',name: 'Crjl',component: Crjl},
  {path: '/wdyy/',name: 'Wdyy',component: Wdyy},
  {path: '/bxcg/',name: 'Bxcg',component: Bxcg,props:true},
  {path: '/cgpage/:id',name: 'CgPage',component: CgPage},
  {path: '/mycenter/',name: 'MyCenter',component: MyCenter},
  {path: '/mycenterrank/',name: 'MyCenterRank',component: MyCenterRank},
  {path: '/jcrecord/',name: 'JcRecord',component: JcRecord},
  {path: '/jcrecorddetail/:id',name: 'JcRecordDetail',component: JcRecordDetail,props:true},
  {path: '/faceimport/',name: 'FaceImport',component: FaceImport},
  {path: '/faceimporth5/',name: 'FaceImportH5',component: FaceImportH5},
  {path: '/xflimit/',name: 'Xflimit',component: Xflimit},
  {path: '/dkinfo/',name: 'DkInfo',component: DkInfo},
  {path: '/dkinfoh5/',name: 'DkInfoH5',component: DkInfoH5},
]

const router = new VueRouter({
  routes
})

export default router
