<template>
	<div class="home">
		<!---->
		<van-nav-bar title="EH5" fixed />
		<van-image class="bannerPic" fit="cover" :src="bannerPic" lazy-load />
		<div class="mainContainer">
			<div class="menuBox">
				<!---->
				<div class="alertBox">
					<div class="alertName"><img src="../assets/imgs/alertTitle.png" />
						<div class="splitLineV"></div>
					</div>
					<div class="scrollBox">
						<van-notice-bar :scrollable="false">
							<van-swipe vertical class="notice-swipe" :autoplay="3000" :show-indicators="false">
								<van-swipe-item v-for="item in newsList">
									<div class="alertItem" @click="$router.push('/newsdetail/'+item.art_id)"><img class="alertSign"
											src="../assets/imgs/alertSign.png" />{{item.title}}</div>
								</van-swipe-item>
							</van-swipe>
						</van-notice-bar>
					</div>
					<div class="more" @click="$router.push('/newsall/')">
						<van-icon name="arrow" color="#999999" style="margin-left:auto;" />
					</div>
				</div>
				<van-divider />
				<!---->
				<van-grid :border="false" icon-size="67.5px" :column-num="4" :gutter="0">
					<van-grid-item v-for="item in menuList" class="menuItem">
						<van-grid-item :icon="item.icon" :text="item.name" @click="$router.push(item.url)"/>
					</van-grid-item>
				</van-grid>
			</div>

			<!---->
			<div class="bookBlockContainer">
				<div class="bookBlockTitle">
					<div class="bookBlockTitle_Left">
						<img src="../assets/imgs/timeColorSign.png" />
						<div></div>
					</div>
					<div class="bookBlockTitle_Right" @click="$router.push('/wdyy/')">
						<van-icon name="arrow" color="#999999" style="margin-left:auto;" />
					</div>
				</div>
				<!---->
				<div class="bookBlockContent" v-if="bookList.length>0">
					<div class="swiper-container">
						<div class="swiper-wrapper">
							<div class="swiper-slide" v-for="item in bookList">
								<div class="bookCard" :class="cardTypeCal(item.status)[0]">
									<div class="bookCardTitle">
										<van-icon :name="cardTypeCal(item.status)[2]" class="cardIcon"/>
										<div>{{cardTypeCal(item.status)[1]}}</div>
									</div>
									<div class="bookCardContent">
										<div class="bookCardList">
											<div class="bookCardList_name"></div>
											<div>{{item.sch}}</div>
										</div>
										<div class="bookCardList">
											<div class="bookCardList_name"></div>
											<div>{{item.type}}</div>
										</div>
										<div class="bookCardList">
											<div class="bookCardList_name"></div>
											<div>{{item.time}}</div>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
				<!---->
				<NoData v-else :noDataText="nodatatext1"></NoData>
			</div>
		</div>
	</div>
</template>

<script>
	import 'swiper/dist/js/swiper'
	import 'swiper/dist/css/swiper.css'
	import Swiper from "swiper"
	import NoData from '../components/NoData/NoData.vue'
	import axios from 'axios'
	export default {
		name: 'Home',
		data() {
			return {
				title: 'E',
				bannerPic: require('../assets/imgs/banner@2x.jpg'),
				nodatatext1:'',
				newsList: [],
				menuList: [
					{
						name: '',
						icon: require('../assets/imgs/menuIcon1.png'),
						url: '/ksyy/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon2.png'),
						url: '/bxcg/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon3.png'),
						url: '/crjl/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon4.png'),
						url: '/mycenter/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon5.png'),
						url: '/jcrecord/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon6.png'),
						url: '/faceimport/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon7.png'),
						url: '/xflimit/'
					},
					{
						name: '',
						icon: require('../assets/imgs/menuIcon8.png'),
						url: '/dkinfo/'
					},
				],
				//-
				bookList:[
				    {sch:'',type:'',time:'2022-02-24 12:00',status:0},
				    {sch:'',type:'',time:'2022-02-24 12:00',status:1},
				    {sch:'',type:'',time:'2022-02-24 12:00',status:2},
				    {sch:'',type:'',time:'2022-02-24 12:00',status:3},
				    {sch:'',type:'',time:'2022-02-24 12:00',status:4},
				]
			}
		},
		computed:{
			//
			cardTypeCal(){
				return function(val){
					switch (val) {
					    case 0:
					      return ['pass','','checked']
					      break;
						case 1:
						  return ['wait','','underway']
						  break;
						case 2:
						  return ['fail','','clear']
						  break;
						case 3:
						  return ['cancel','','warning']
						  break;
						case 4:
						  return ['overdue','','delete']
						  break;
					  }
				}
			}
		},
		methods:{
			//
			async getNewsList(){
				const res = await axios.get('http://toutiao.itheima.net/v1_0/articles',{
					params:{
						channel_id:22,
						timestamp:1655275698
					}
				})
				if(res.status == 200){
					this.newsList = res.data.data.results
				}
				
			}
		},
		components:{
			NoData
		},
		mounted() {
			new Swiper('.swiper-container', {
				autoplay: { 
					delay: 2500, 
					disableOnInteraction: false, 
				},
				loop:false,
				slidesPerView: 'auto',
				centeredSlides: true,  
				paginationClickable: true,
				effect: 'left',
			})
		},
		created(){
			this.getNewsList()
		}
	}
</script>
