<template>
	<div class="NewsAllContainer">
		<!---->
		<van-nav-bar title="" left-text="" left-arrow fixed
			@click-left="$router.push({path: '/', replace: true})" />
		<div class="container">
			<div class="alertList" v-for="item in allAlertList" v-if="allAlertList.length>0" @click="$router.push('/newsdetail/'+item.art_id)">
				<div class="alertList_pic">
					<img src="../assets/imgs/alertIcon.png" />
				</div>
				<div class="alertListContent">
					<div class="alertListContent_left">
						<div class="alertListContent_name">{{item.title}}</div>
						<div class="alertListContent_detail">
							<div class="alertListContent_school"><van-icon class="vanIcon" name="wap-home-o" color="#999999" />{{item.aut_name}}</div>
							<div class="alertListContent_value"><van-icon class="vanIcon" name="clock-o" color="#999999" />{{item.pubdate}}</div>
						</div>
					</div>
				</div>
				<div class="alertListContent_right">
					<van-icon name="arrow" color="#999999" class="arrow" />
				</div>
			</div>
			
			<NoData v-else :noDataText="nodatatext1"></NoData>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	import NoData from '../components/NoData/NoData.vue'
	export default{
		data(){
			return{
				//
				allAlertList:[],
				nodatatext1:'',
			}
		},
		methods:{
			//
			async getNewsList(){
				const res = await axios.get('http://toutiao.itheima.net/v1_0/articles',{
					params:{
						channel_id:22,
						timestamp:1655275698
					}
				})
				if(res.status == 200){
					this.allAlertList = res.data.data.results
				}
				
			}
		},
		created(){
			this.getNewsList()
		}
	}
</script>

